import { donate, donationInfo } from "@/api/donation.js";

const donationStore = {
  namespaced: true,
  state: {
    donationInfo: [],
  },

  getters: {
    donationInfo(state) {
      return state.donationInfo;
    },
  },

  mutations: {
    SET_DONATIONINFO: (state, donationInfo) => {
      state.donationInfo = donationInfo;
    },
  },

  actions: {
    //  
    async getDonationInfo({ commit }, foundationSeq) {
      await donationInfo(
        foundationSeq,
        (response) => {
          console.log(" ");
          console.log(response.data);
          commit("SET_DONATIONINFO", response.data);
        },
        (error) => {
          console.log(error);
        }
      );
    },

    // 
    // donationInfo = {
    //   "donationImgUrl": "string",
    //   "donationPay": 100000,
    //   "donationText": "",
    //   "foundationSeq": 1,
    //   "userSeq": 1
    // }
    async doDonate(donationInfo) {
      console.log(donationInfo);
      await donate(
        donationInfo,
        (response) => {
          console.log("!");
          console.log(response);
        },
        (error) => {
          console.log(error);
        }
      );
    },
  },
};

export default donationStore;
